      </b-row>
      <b-row class="mt-3" v-if="mill_selected & mill_loading">
        <h4>The Model is running. Please wait a moment.</h4>
      </b-row>
      <b-col v-if="mill_selected & !mill_loading">
        <b-row class="justify-content-md-center">
          <b-col cols="6" v-if="!train_loaded">
            <b-row class="mt-3">
              <h5>Training Results</h5>
              <p>
                No Data for this Mill.
              </p>
              </b-row>
          </b-col>
          <b-col cols="6" v-if="train_loaded">
            <b-row class="mt-3">
              <h5>Training Results</h5>
              <p>
                The model used the below data to train on. The model attempted
                to generalised the patterns that exist in the data.
              </p>
              <apexchart
                width="800"
                type="bar"
                :options="chartOptionsTrain"
                :series="seriesTrain"
              ></apexchart
            ></b-row>
          </b-col>
        </b-row>

        <b-row class="justify-content-md-center">

          <b-col cols="6" v-if="!test_loaded">
            <b-row class="mt-3">
              <h5>Testing Results</h5>
              <p>
                No Data for this Mill.
              </p>
            </b-row>
          </b-col>
          <b-col cols="6" v-if="test_loaded">
            <b-row class="mt-3">
              <h5>Testing Results</h5>
              <p>
                The model was used to predict below data, in order to evaluate
                its abilty to generalise the Training data, This data is unseen.
              </p>
              <apexchart
                width="800"
                type="bar"
                :options="chartOptionsTest"
                :series="seriesTest"
              ></apexchart>
            </b-row>
          </b-col>
        </b-row>

        <b-row class="justify-content-md-center">
          <b-col cols="6" v-if="!eval_loaded">
            <b-row class="mt-3">
              <h5>2021 Predictions</h5>
              <p>No Data for this Mill.</p>
            </b-row>
          </b-col>
          <b-col cols="6" v-if="eval_loaded">
            <b-row class="mt-3">
              <h5>2021 Predictions</h5>
              <p>The model attempted to predict the RV% for the 2021 season.</p>
              <apexchart
                width="800"
                type="bar"
                :options="chartOptionsEval"
                :series="seriesEval"
              ></apexchart>
            </b-row>
          </b-col>
        </b-row>
      </b-col>
