<script>
export default {
  components: {},
  data: function () {
    return {
      chartOptionsTrain: {
        chart: {
          id: "Training_Predictions",
        },
        yaxis: {
          min: 10,
          max: 15,
          decimalsInFloat: 1,
          title: { text: "RV %" },
        },
        dataLabels: {
          enabled: false,
        },
        xaxis: {
          categories: [],
        },
      },
      seriesTrain: [
        {
          name: "FC Forecasted RV%",
          data: [],
        },
        {
          name: "Model Predicted RV%",
          data: [],
        },
        {
          name: "Seasonal RV%",
          data: [],
        },
      ],
      chartOptionsTest: {
        chart: {
          id: "Testing_Predictions",
        },
        yaxis: {
          min: 10,
          max: 15,
          decimalsInFloat: 1,
          title: { text: "RV %" },
        },
        dataLabels: {
          enabled: false,
        },
        xaxis: {
          categories: [],
        },
      },
      seriesTest: [
        {
          name: "FC Forecasted RV%",
          data: [],
        },
        {
          name: "Model Predicted RV%",
          data: [],
        },
        {
          name: "Seasonal RV%",
          data: [],
        },
      ],
      chartOptionsEval: {
        chart: {
          id: "2021_Predictions",
        },
        yaxis: {
          min: 10,
          max: 15,
          decimalsInFloat: 1,
          title: { text: "RV %" },
        },
        dataLabels: {
          enabled: false,
        },
        xaxis: {
          categories: [],
        },
      },
      seriesEval: [
        {
          name: "FC Forecasted RV%",
          data: [],
        },
        {
          name: "Model Predicted RV%",
          data: [],
        },
      ],
      test: "tesgfsgdt",
      raw: null,
      train_loaded: false,
      test_loaded: false,
      eval_loaded: false,
      train_empty: true,
      test_empty: true,
      eval_empty: true,
      mill: "",
      mills: [
        "Amatikulu",
        "Darnall",
        "Eston",
        "Felixton",
        "Gledhow",
        "Komati",
        "Maidstone",
        "Malelane",
        "Noodsberg",
        "Pongola",
        "Sezela",
        "UCL",
        "Umfolozi",
        "Umzimkulu",
      ],
      api: "http://3.18.113.241:5000/?mill=",
      mill_selected: false,
      mill_loading: false,
    };
  },
  methods: {
    async getData() {
      try {
        const response = await this.$http.get(this.api + this.mill);
        // JSON responses are automatically parsed.
        // console.log(response.data);
        this.raw = response.data;
        if (this.raw.train && Object.keys(this.raw.train).length) {
          const train_keys = Object.keys(this.raw.train);
          // for (var key in Object.keys(this.raw.train)){
          train_keys.forEach((key) => {
            console.log(key);
            this.chartOptionsTrain.xaxis.categories.push(
              "" +
                this.raw.train[key].fa_SEASON +
                "-" +
                this.raw.train[key].SUGARMONTH
            );
            this.seriesTrain[0].data.push(this.raw.train[key].FC_FORECAST);
            this.seriesTrain[1].data.push(this.raw.train[key].Prediction);
            this.seriesTrain[2].data.push(this.raw.train[key].Actual_RV);
          });
          this.train_loaded = true;
        } else {
          this.train_empty = false;
        }
        if (this.raw.test && Object.keys(this.raw.test).length) {
          const test_keys = Object.keys(this.raw.test);
          // for (var key in Object.keys(this.raw.train)){
          test_keys.forEach((key) => {
            console.log(key);
            this.chartOptionsTest.xaxis.categories.push(
              "" +
                this.raw.test[key].fa_SEASON +
                "-" +
                this.raw.test[key].SUGARMONTH
            );
            this.seriesTest[0].data.push(this.raw.test[key].FC_FORECAST);
            this.seriesTest[1].data.push(this.raw.test[key].Prediction);
            this.seriesTest[2].data.push(this.raw.test[key].Actual_RV);
          });
          this.test_loaded = true;
        } else {
          this.test_empty = false;
        }
        if (this.raw.eval && Object.keys(this.raw.eval).length) {
          const eval_keys = Object.keys(this.raw.eval);
          // for (var key in Object.keys(this.raw.train)){
          eval_keys.forEach((key) => {
            console.log(key);
            this.chartOptionsEval.xaxis.categories.push(
              "" +
                this.raw.eval[key].fa_SEASON +
                "-" +
                this.raw.eval[key].SUGARMONTH
            );
            this.seriesEval[0].data.push(this.raw.eval[key].FC_FORECAST);
            this.seriesEval[1].data.push(this.raw.eval[key].Prediction);
          });
          this.eval_loaded = true;
        } else {
          this.tval_empty = false;
        }
        this.mill_loading = false;
      } catch (error) {
        console.log(error);
      }
    },
    mill_changed() {
      this.seriesTrain[0].data = [];
      this.seriesTrain[1].data = [];
      this.seriesTrain[2].data = [];
      this.seriesTest[0].data = [];
      this.seriesTest[1].data = [];
      this.seriesTest[2].data = [];
      this.seriesEval[0].data = [];
      this.seriesEval[1].data = [];
      this.chartOptionsTrain.xaxis.categories = [];
      this.chartOptionsTest.xaxis.categories = [];
      this.chartOptionsEval.xaxis.categories = [];
      this.train_empty = true;
      this.trest_empty = true;
      this.train_empty = true;

      (this.train_loaded = false),
        (this.test_loaded = false),
        (this.eval_loaded = false),
        console.log(this.api + this.mill);
      this.mill_selected = true;
      this.mill_loading = true;
      this.getData();
    },
  },
  mounted() {
    // this.getData()
  },
};
</script>
